.Header {
  max-width: 200%;
  width: 100%;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 2;
  font-family: 'Courier New', Courier, monospace;
  border-bottom: 0.01rem solid #0d0019;
  background: linear-gradient(to bottom, #0b0423, #161532);
  flex-wrap: wrap;
  overflow-x: auto;
  max-height: max-content;
}

.head {
  color: #ffffff;
  font-weight: 600;
  margin-top: 1rem;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 1;
  margin-left: 1rem;
}

.head h2 {
  margin-right: 44rem;
}

.head p {
  margin-right: 5rem;
}

.head p {
  margin-right: 44rem;
}

.navbar {
  max-width: 100%;
  margin: 0 auto;
}

ul {
  display: flex;
  text-align: right;
  justify-content: flex-end;
}

ul li {
  margin: 1.5rem;
  list-style: none;
  text-align: right;
}

ul li a {
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(to right, #ffffff, #ffffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

ul li a:hover {
  border-bottom: 3px solid rgb(172, 175, 242);
}

.changer {
  display: flex;
  margin-right: 3.5rem;
}

.changer .menu {
  display: none;
  cursor: pointer;
  margin-right: 1rem;
}

.closed {
  display: none;
}

.closed .close {
  display: none;
}

@media (max-width: 800px) {
  .Header {
    justify-content: space-between;
    position: relative;
  }

  .navbar {
    display: none;
    transition: ease;
  }

  .navbar.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    inset: 0;
    background: whitesmoke;
    transition: 1400ms ease;
  }

  ul {
    margin: 0;
    align-items: center;
    flex-direction: column;
    margin-top: 1rem;
  }

  ul li {
    margin-block: 2rem;
  }

  ul li a {
    font-size: 20px;
    color: black;
    font-weight: bolder;
  }

  .changer .menu {
    display: block;
  }

  .closed {
    display: block;
  }

  .closed .close {
    display: block;
  }

  .Header_logo {
    margin-left: 0.450rem;
  }
}

@media (max-width: 350px) {
  ul {
    margin: 0;
    align-items: center;
    flex-direction: column;
    margin-top: 1rem;
  }

  ul li {
    margin-block: 2rem;
  }

  ul li a {
    font-size: 20px;
    color: black;
    font-weight: bolder;
  }

  .changer .menu {
    display: block;
    margin-right: 0;
  }
}

@media (max-width: 1000px) {
  .head {
    margin-right: 5%;
    margin-left: 10%;
    display: flex;
    align-items: center;
  }

  .head h2 {
    margin-right: 2rem;
  }

  .head p {
    margin-right: 0;
  }
}
