.main_container {
  display: flex;
  align-items: center;
  flex-wrap: wrap; /* Allow wrapping on smaller screens */
  position: relative; /* Add relative positioning */
  padding-bottom: 10rem;
}


.text {
  flex: 1;
  text-transform: uppercase;
  margin-left: -10rem;
  background-image: linear-gradient(to right, #98edf5, #eceef7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Courier New';
  font-weight: 900;
  position: absolute;
  font-size: 18px;
  margin-top: 1.5rem;
}

p {
  margin-bottom: 0.5rem;
}

.text .dev {
  font-size: 10px;
}

.icons .icon {
  margin-left: 1rem;
  color: rgb(168, 224, 246);
  position: relative;
}

.icons .icon1 {
  color: rgb(168, 224, 246);
  position: relative;
}

.image {
  display: flex;
  justify-content: flex-end;
  margin-left: 10rem;
  margin-right: -5rem;
  padding: 10x;
}

.image img {
  max-width: 20%;
  max-height: 20%;
  width: 20%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  margin-top: 1rem;
  margin-left: 40rem;
}

.main {
  padding: 205px; /* Adjust the top padding to create space for the header */
  position: relative; /* Add relative positioning */
  z-index: 1; /* Add a lower z-index value to keep it below the main content */
}


h1 {
  font-size: 54px; /* Adjust the font size for smaller screens */
  font-family: 'Courier New', Courier, monospace;
}

.text .dev {
  font-size: 10px;
}

.icons {
  display: flex;
  margin-top: 1rem;
}

.icons .icon {
  margin: 0 0.5rem; /* Add some space between the icons */
  color: rgb(168, 224, 246);
  position: relative;
}

.image {
  display: flex;
  justify-content: center; /* Center the image on smaller screens */
  padding: 0px;
  margin-top: 1rem;
}

.image img {
  max-width: 20%; /* Make the image responsive by adjusting the width */
  max-height: auto; /* Allow the height to adjust based on the image aspect ratio */
  width: auto;
  aspect-ratio: 1/1;
  border-radius: 50%;
}

/* Media query for screens with a maximum width of 768px (mobile) */
@media (max-width: 768px) {
  .main {
    padding: 30px;
  }

  h1 {
    font-size: 24px; /* Adjust the font size for mobile */
  }

  .text {
    margin-left: 0;
    justify-content: center;
  }

  .image {
    margin-left: 0;
    display: none;
  }
}
