.about-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 130px;
  position: relative;
  padding: 10rem;
}

.about-container h1 {
  margin-right: 1rem;
  background-image: linear-gradient(to right, #98edf5, #eceef7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.paragraph-wrapper {
  display: inline-block;
}

.paragraph-border {
  border-left: 4px solid black;
  padding-left: 1rem;
  width: 400px; /* Adjust the width to your desired size */
  height: 380px; /* Adjust the height to your desired size */
  overflow: hidden;
  border-color: rgb(213, 238, 244);
}

.paragraph-border p {
  margin: 0;
  word-wrap: break-word; /* Add this line to enable word wrapping */
  background-image: linear-gradient(to right, #ffffff, #98edf5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
}

/* Media query for screens with a maximum width of 768px (mobile) */
@media (max-width: 768px) {
  .about-container {
    padding: 30px; /* Adjust the padding for mobile */
  }

  .paragraph-border {
    width: auto; /* Remove the fixed width for mobile */
    height: auto; /* Remove the fixed height for mobile */
  }
}
